import { AgGridModule } from '@ag-grid-community/angular';
import { Component, OnInit, NgModule, Input, Output, EventEmitter } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { AllCommunityModules, Module } from '@ag-grid-community/all-modules';






@Component({
  selector: 'app-data-grid',
  template: `<div class="container" style="margin: 50px;">


    
    <!-- <button class="btn btn-primary" (click)="removeElement()" > borrar </button> -->
    <button (click)="removeData()">Remove</button>
    <button (click)="newData()">New</button>
    <div class="row">
        <div class="col-12 text-left" >
            <label>Search </label>
            <input type="text" placeholder="" (keyup)="quickSearch()" [(ngModel)]="searchValue" ml-2 >
            
        </div>
    </div>

    <div class="row">
        <div class="ag-theme-balham col-12" id="myGrid">
            <ag-grid-angular
            style=" width: 750px; height: 750px;"
            class="ag-theme-balham"
            [floatingFilter]="true"
            [rowData]="rowData"
            [columnDefs]="columnDefs"
            [animateRows]="true"
            [pagination]="false"
            [modules]="modules"            
            rowSelection="multiple"
            (gridReady)="onGridReady($event)">
            </ag-grid-angular>
        </div>
    </div>
</div>

`,
  styles: [`label{display:inline-block;margin-right:5px;margin-left:5px;margin-top:5px}`]
})
export class DataGridComponent implements OnInit {

  modules: Module[] = AllCommunityModules;
  searchValue: string;
  private gridApi;
  private gridColumnApi;
  @Input() columnDefs: any[];
  rowData: any[];
  @Input() getAll: () => Observable<any>;

  @Output() remove: EventEmitter<any[]>;
  @Output() new: EventEmitter<boolean>;


  constructor() {
    this.remove = new EventEmitter();
    this.new = new EventEmitter();
  }

  ngOnInit() {
  }

  onGridReady(params){
    this.gridApi = params.api;
    this.gridColumnApi = params.columnApi;
    this.gridApi.rowHeight = 100;
    this.getElements();
    params.api.sizeColumnsToFit();

  }

  quickSearch(){
      this.gridApi.setQuickFilter(this.searchValue);
  }

  getElements()
  {
    this.getAll()
    .subscribe((items) =>{
        console.log(items);
        this.rowData=items;
       // this.gridApi.setRowData(items);

    });
  }

  removeData() {
    let selectedNodes = this.gridApi.getSelectedNodes();
  	let selectedData = selectedNodes.map(node => node.data);
    console.log(selectedData);
    this.remove.emit(selectedData);
}

  newData()
  {
    this.new.emit(true);
  }


}
